.minimizedMyQueenElements {
  height: 5rem;
}

.minimizedCardDisplayContainer {
  height: 12.5rem;
  padding: 0.5rem;
  margin-bottom: 0rem;
  margin-top: 0rem;
}

.minimizedQueenCardImageContainer {
  height: 0rem;
}

.minimizedCardQueenImageProp {
  scale: 0;
}

.minimizedCardQueenNameProp {
  font-size: 1.25rem;
  height: 3.5rem;
}

.minimizedqueenStatsWithoutNameContainer * {
  margin-bottom: 0.25rem;
}

.minimizedButtonContainer {
  margin-top: 0.5rem;
}

.minimizedCardQueenLinkProp {
  display: none;
}

@media (max-width: 1340px) {
  .cardDisplayContainer {
    height: 25rem;
    width: 12rem;
    /* min-height: 25rem; */
    min-width: 12rem;
  }

  .queenCardImageContainer {
    height: 13rem;
  }

  .cardQueenImageProp {
    max-width: 9rem;
    max-height: 11rem;
  }

  .cardQueenNameProp {
    font-size: 1.2rem;
    height: 3.5rem;
  }

  .queenStatsWithoutNameContainer * {
    margin-bottom: 0.25rem;
    font-size: 0.95rem;
  }

  .cardQueenHomepageProp,
  .cardQueenChallengeWinsProp {
    font-size: 0.8rem;
  }
  .cardQueenLinkProp:link {
    font-size: 0.8rem;
  }
  .buttonContainer {
    margin-top: 0.5rem;
  }
  .btn2 {
    font-size: 0.75rem;
    width: 6rem;
  }

  .minimizedCardDisplayContainer {
    height: 11.5rem;
    padding: 0.5rem;
  }

  .minimizedQueenCardImageContainer {
    height: 0rem;
  }

  .minimizedCardQueenImageProp {
    scale: 0;
  }

  .minimizedCardQueenNameProp {
    font-size: 1.15rem;
  }
}

@media (max-width: 1100px) {
  .cardDisplayContainer:first-of-type {
    margin-left: 1rem;
  }

  .cardDisplayContainer:last-of-type {
    margin-right: 1rem;
  }

  .minimizedQueenCardImageContainer {
    height: 0rem;
  }

  .minimizedCardQueenImageProp {
    scale: 0;
  }
}
